export default {
	data() {
		return {
			searchGoods: null,
			currentIndex: 0,
			isNull: true,
			flag1: true,
			flag2: true,
			keyWords: '',
			stopLoad: false,
			//请求参数(筛选条件)
			data: null,
			page: 1,
			cat_id: null,
			currentCondition:0,
			isRise:false,
		}
	},
	onLoad(options) {
		console.log(options)
		this.keyWords = options.keyWords
		if (options.cat_id) {
			this.cat_id = options.cat_id
			this.getCategoryList()
		} else {
			this.getSearchList()
		}
	},
	methods: {
		chooseOption(i){
			this.currentCondition = i
			if(this.currentCondition!=1){
				this.isRise = false
			}else{
				this.isRise = !this.isRise
			}
			if(this.currentCondition==0){
				this.showToast('按综合排序',1000)
			}else if(this.currentCondition==1){
				if(this.isRise){
					this.showToast('按价格升序',1000)
				}else{
					this.showToast('按价格降序',1000)
				}
				
			}else{
				this.showToast('按销量排序',1000)
			}
		},
		toDetail(goods_id, shop_id) {
			uni.navigateTo({
				url: "../goodsDetail/goodsDetail?id=" + goods_id + "&shop_id=" + shop_id
			})
		},
		clearKey() {
			this.keyWords = ''
		},
		//显示点击过的分类商品列表
		getCategoryList() {
			this.data = {
				token: uni.getStorageSync('token'),
				pages: 1,
				cat_id: this.cat_id
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: this.data,
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.searchGoods = res.data.data
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		//显示搜索列表
		getSearchList() {
			if (this.keyWords == '') {
				this.showToast('请输入搜索关键词')
				return
			}
			this.searchGoods = []
			this.currentIndex = 0
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
			this.data = {
				token: uni.getStorageSync('token'),
				keywords: this.keyWords,
				pages: this.page
			}
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: this.data,
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.searchGoods = res.data.data
					}
				}
			})
		},
		//获取排序后的商品列表
		getSortGoodsList() {
			console.log(this.data)
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: this.data,
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.searchGoods = res.data.data.goods
					}
				}
			})
		},

		//加载更多商品
		loadMoreGoods() {
			console.log(this.stopLoad)
			if (this.stopLoad) {
				return
			}
			this.page++
			this.data.pages = this.page
			console.log(this.data)
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/goods/goodsList",
				data: this.data,
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if (res.data.data.length == 0) {
							this.stopLoad = true
							return
						}
						for (var i = 0; i < res.data.data.length; i++) {
							this.searchGoods.push(res.data.data[i])
						}
					}
				}
			})
		}
	},
	//页面触底加载更多精选商品
	onReachBottom() {
		console.log('触底了')
		this.loadMoreGoods()
	}
}
